<template>
    <div>
        <el-form ref="form" :rules="form_rules" :model="form" label-width="120px" size="small">
            
            <el-form-item label="" prop="linkman_name">
                <el-input v-model="form.linkman_name"></el-input>
            </el-form-item>
            <el-form-item label="" prop="telephone">
                <el-input v-model="form.telephone"></el-input>
            </el-form-item>
            <el-form-item label="">
                <el-radio-group v-model="form.linkman_sex">
                    <el-radio label="1"></el-radio>
                    <el-radio label="0"></el-radio>
                </el-radio-group>
            </el-form-item>
            <el-form-item label="">
                <el-select v-model="form.identification_type">
                    <el-option v-for="item in identification_type" :key="item.value_id" :label="item.value_name" :value="item.value_id">{{item.value_name}}</el-option>
                </el-select>
            </el-form-item>
            <el-form-item label="">
                <el-input v-model="form.identification_number"></el-input>
            </el-form-item>
            <el-form-item label="">
                <el-radio-group v-model="form.linkman_type">
                    <el-radio v-for="item in linkman_type" :key="item.value_id" :label="String(item.value_id)">{{item.value_name}}</el-radio>
                </el-radio-group>
            </el-form-item>
            <el-form-item label="">
                <el-input type="textarea" v-model="form.remark" :rows="3"></el-input>
            </el-form-item>
            <el-form-item>
                <el-button type="primary" @click="submit()"></el-button>
                <el-button></el-button>
            </el-form-item>
        </el-form>
    </div>
</template>

<script>
export default {
    data () {
        return {
            form: {
                c_id: 0,
                linkman_name: '', // 
                telephone: '', // 
                linkman_type: '1', // 
                linkman_sex: '1', // 
                identification_type: '', // 
                identification_number: '', // 
                remark: '' // 
            },
            form_rules: {
                linkman_name: [
                    { required: true, message: '', trigger: 'blur'}
                ],
                telephone: [
                    { required: true, message: '', trigger: 'blur'}
                ]
            },
            identification_type: [], //  - 
            linkman_type: [{ // 
                value_id: '1',
                value_name: ''
            },{
                value_id: '2',
                value_name: ''
            },{
                value_id: '3',
                value_name: ''
            },{
                value_id: '4',
                value_name: ''
            }]
        }
    },
    created() {
        let _this = this;
        this.$https.all([
            this.getDic('contact_card_type', 'identification_type')
        ]).then(this.$https.spread(function (acct, perms) {
        }))
    },
    methods: {
        submit() { // 
            this.$refs.form.validate((valid) => {
                if (valid) {
                    let _postData = JSON.parse(JSON.stringify(this.form));


                    _postData.identification_type = this.form.identification_type == '' ? 0 : this.form.identification_type;

                    _postData = JSON.stringify(_postData);
                    this.$message({
                        type: 'success',
                        showClose: true,
                        message: '',
                        duration: 1000,
                        onClose: () => {
                            // dialog
                            this.closeDialog('dialogContactsAdd');
                            // 
                            this.$emit('parentSetContactsList', _postData);
                        }
                    })
                } else {
                    console.error('');
                    return false;
                }
            });
        },
        getDic(code, dic) { //  
            this.$https.get('/api/Dicts/GetValues', {
                params: {
                    type_code: code
                }
            }).then((result) => {
                if (result.data.code == 0) {
                    this[dic] = result.data.data;
                } else {
                    this.$message({
                        type: 'error',
                        showClose: true,
                        message: result.data.message
                    })
                }
            })
        },
        closeDialog(name) { // 
            // this.$refs.form.resetFields(); // 
            if (!name) {
                this.$emit('closeThisDialog', 'dialogContactsAdd'); // 
                this.$emit('closeThisDialog', 'dialogContactsEdit'); // 
            } else {
                this.$emit('closeThisDialog', name); // 
            }
        }
    }
}
</script>
<style scoped>
    .el-radio {
        line-height: 32px;
        margin-right: 30px;
    }
    .el-radio+.el-radio {
        margin-left: 0px;
    }
</style>


