<template>
    <div>
        <el-tabs :value="house_type" type="card" @tab-click="handleClick">
            <el-tab-pane label="" name="0" :disabled="!!id"></el-tab-pane>
            <el-tab-pane label="" name="1" :disabled="!!id"></el-tab-pane>
        </el-tabs>
        <el-form ref="form" :rules="form_rules" :model="form" label-width="80px" size="small">
            <div v-show="house_type === '0' && !id">

                <el-form-item label="" prop="room_id"> 
                    <el-cascader
                        clearable
                        :options="estate"
                        v-model="estateVal"
                        separator="#"
                        @active-item-change="handleItemChange"
                        @change="handleChange"
                        >
                    </el-cascader>
                </el-form-item>
            </div>
            <el-form-item label="" v-show="house_type === '0' && id"> 
                <p>{{form.resource_name}}</p>
            </el-form-item>
            <div v-show="house_type === '1'">
                <el-row>
                    <el-col :span="8">
                        <el-form-item label=""> 
                            <el-input v-model="form.city" placeholder=""></el-input> 
                        </el-form-item>
                    </el-col>
                    <el-col :span="8">
                        <el-form-item label=""> 
                            <el-input v-model="form.region" placeholder=""></el-input> 
                        </el-form-item>
                    </el-col>
                    <el-col :span="8">
                        <el-form-item label="" prop="resource_name"> 
                            <el-input v-model="form.resource_name" placeholder=""></el-input> 
                        </el-form-item>
                    </el-col>
                </el-row>
                <el-row>
                    <el-col :span="6">
                        <el-form-item label=""> 
                            <el-input v-model="form.building" placeholder=""></el-input> 
                        </el-form-item>
                    </el-col>
                    <el-col :span="6">
                        <el-form-item label=""> 
                            <el-input v-model="form.unit" placeholder=""></el-input> 
                        </el-form-item>
                    </el-col>
                    <el-col :span="6">
                        <el-form-item label=""> 
                            <el-input v-model="form.floor" placeholder=""></el-input> 
                        </el-form-item>
                    </el-col>
                    <el-col :span="6">
                        <el-form-item label=""> 
                            <el-input v-model="form.roomno" placeholder=""></el-input> 
                        </el-form-item>
                    </el-col>
                </el-row>
                <el-row>
                    <el-col :span="18">
                        <el-form-item label=""> 
                            <el-input v-model="form.addr" placeholder=""></el-input> 
                        </el-form-item>
                    </el-col>
                    <el-col :span="6">
                        <el-form-item label=""> 
                            <el-input v-model="form.period" placeholder=""></el-input> 
                        </el-form-item>
                    </el-col>
                </el-row>
            </div>
            
            <div class="hr-line-dashed"></div>        
            <el-row>
                <el-col :span="12">
                    <el-form-item label="">
                        <el-select v-model="form.room_unit">
                            <el-option label="" value=""></el-option>
                            <el-option v-for="item in room_unit" :key="item.value_id" :label="item.value_name" :value="item.value_id.toString()"></el-option>
                        </el-select>
                    </el-form-item>
                </el-col>
                <el-col :span="12">
                    <el-form-item label="">
                        <el-select v-model="form.room_decoration">
                            <el-option label="" value=""></el-option>
                            <el-option v-for="item in room_decoration" :key="item.value_id" :label="item.value_name" :value="item.value_id.toString()"></el-option>
                        </el-select> 
                    </el-form-item>
                </el-col>
            </el-row>
            <el-row>
                <el-col :span="12">
                    <el-form-item label="">
                        <el-select v-model="form.room_toward">
                            <el-option label="" value=""></el-option>
                            <el-option v-for="item in room_toward" :key="item.value_id" :label="item.value_name" :value="item.value_id.toString()"></el-option>
                        </el-select> 
                    </el-form-item>
                </el-col>
                <el-col :span="12">
                    <el-form-item label="" prop="room_feature">
                        <el-select v-model="form.room_feature">
                            <el-option label="" value=""></el-option>
                            <el-option v-for="item in room_feature" :key="item.value_id" :label="item.value_name" :value="item.value_id.toString()"></el-option>
                        </el-select>
                    </el-form-item>
                </el-col>
            </el-row>
            <el-row>
                <el-col :span="12">
                    <el-form-item label=""> 
                        <el-input v-model="form.built_area" @keyup.native="onkeyupPrice($event)" placeholder="" type="number" style="width: 150px"></el-input> 
                    </el-form-item>
                </el-col>
                <el-col :span="12">
                    <el-form-item label="">
                        <el-input v-model="form.usable_area" @keyup.native="onkeyupPrice($event)" placeholder="" type="number" style="width: 150px"></el-input> 
                    </el-form-item>
                </el-col>
            </el-row>
            <el-row>
                <el-col :span="12">
                    <el-form-item label=""> 
                        <el-input v-model="form.building_age" placeholder=""></el-input> 
                    </el-form-item>
                </el-col>
                <el-col :span="12">
                    <el-form-item label="">
                        <el-input v-model="form.manage_cost" @keyup.native="onkeyupPrice($event)" placeholder="/" type="number" style="width: 150px"></el-input> /
                    </el-form-item>
                </el-col>
            </el-row>
            <el-row>
                <el-col :span="12">
                    <el-form-item label=""> 
                        <el-input v-model="form.property_right" placeholder=""></el-input> 
                    </el-form-item>
                </el-col>
                <el-col :span="12">
                    <el-form-item label=""> 
                        <el-input v-model="form.come_from" placeholder=""></el-input> 
                    </el-form-item>
                </el-col>
            </el-row>
            <el-row>
                <el-col :span="12">
                    <el-form-item label=""> 
                        <el-input v-model="form.brokerage" placeholder=""></el-input> 
                    </el-form-item>
                </el-col>
                <el-col :span="12">
                    <el-form-item label=""> 
                        <el-date-picker v-model="form.expiration" type="date" placeholder=""></el-date-picker>
                    </el-form-item>
                </el-col>
            </el-row>
            <el-row>
                <el-col :span="12">
                    <el-form-item label=""> 
                        <el-input v-model="form.check_way" placeholder=""></el-input> 
                    </el-form-item>
                </el-col>
                <el-col :span="12">
                    <el-form-item label=""> 
                        <el-input v-model="form.entrust_way" placeholder=""></el-input> 
                    </el-form-item>
                </el-col>
            </el-row>
            <el-row>
                <el-col :span="12">
                    <el-form-item label="" prop="status">
                        <el-select v-model="form.status">
                            <el-option v-for="item in statusBase" :key="item.value" :label="item.name" :value="item.value.toString()"></el-option>
                        </el-select>
                    </el-form-item>
                </el-col>
                <el-col :span="12">
                    <el-form-item label="" prop="rent_charge">
                        <el-input v-model="form.rent_charge1" @keyup.native="onkeyupPrice($event)" placeholder="0.00" type="number" style="width: 100px"></el-input> ~
                        <el-input v-model="form.rent_charge2" @keyup.native="onkeyupPrice($event)" placeholder="0.00" type="number" style="width: 100px"></el-input> /
                    </el-form-item>
                </el-col>
            </el-row>
            <el-row>
                <el-col :span="12">
                    <el-form-item label="" prop="sale_charge">
                        <el-input v-model="form.sale_charge1" @keyup.native="onkeyupPrice($event)" placeholder="0.00" type="number" style="width: 100px"></el-input> ~
                        <el-input v-model="form.sale_charge2" @keyup.native="onkeyupPrice($event)" placeholder="0.00" type="number" style="width: 100px"></el-input> /
                    </el-form-item>
                </el-col>
                <el-col :span="12">
                    <el-form-item label="">
                        <el-input v-model="form.base_charge" @keyup.native="onkeyupPrice($event)" placeholder="" type="number" style="width: 150px"></el-input> /
                    </el-form-item>
                </el-col>
            </el-row>
                    
            <div class="hr-line-dashed"></div>
            <el-row>
                <el-col :span="24">
                    <el-form-item label="">
                        <el-input v-model="form.user_name1" placeholder="" type="text" style="width: 240px"></el-input>
                        <el-input v-model="form.user_mobile1" placeholder="" type="text" style="width: 240px"></el-input>
                        <el-input v-model="form.remark1" placeholder="" type="text" style="width: 240px"></el-input>
                    </el-form-item>
                </el-col>
            </el-row>
            <el-row>
                <el-col :span="24">
                    <el-form-item label="">
                        <el-input v-model="form.user_name2" placeholder="" type="text" style="width: 240px"></el-input>
                        <el-input v-model="form.user_mobile2" placeholder="" type="text" style="width: 240px"></el-input>
                        <el-input v-model="form.remark2" placeholder="" type="text" style="width: 240px"></el-input>
                    </el-form-item>
                </el-col>
            </el-row>

            <el-form-item>
                <el-button type="primary" @click="submit()"></el-button>
                <el-button @click="closeDialog()"></el-button>
            </el-form-item>
        </el-form>
    </div>
</template>

<script>
export default {
    data () {

        let validate_room_id = (rule, value, callback) => {

            if (this.form.room_id === '' && this.house_type === '0') { //  
                callback(new Error(''));
            } else {
                callback();
            }
        };
        let validate_rent_charge = (rule, value, callback) => {

            if ((this.form.status === '0' || this.form.status === '2') && this.form.rent_charge1 === '') { //  
                callback(new Error(''));
            } else if ( (this.form.rent_charge1 !== '' && this.form.rent_charge2 !== '') || (this.form.rent_charge1 === '' && this.form.rent_charge2 === '') ) {
                callback();
            } else {
                callback(new Error(''));
            }
        };
        let validate_sale_charge = (rule, value, callback) => {
            
            if ((this.form.status === '1' || this.form.status === '2') && this.form.sale_charge1 === '') { //  
                callback(new Error(''));
            } else if ( (this.form.sale_charge1 !== '' && this.form.sale_charge2 !== '') || (this.form.sale_charge1 === '' && this.form.sale_charge2 === '') ) {
                console.log('xxx')
                callback();
            } else {
                callback(new Error(''));
            }
        };
        return {
            house_type: '0',
            form: {
                resource_name: '', // 
                city: '', // 
                region: '', // 
                building: '', // 
                roomno: '', // 
                unit: '', // 
                floor: '', // 
                addr: '', // 
                period: '', // 

                room_id: '', // 

                room_unit: '', // 
                room_decoration: '', // 
                room_toward: '', // 
                built_area: '', // 
                usable_area: '', // 
                room_feature: '', // 
                building_age: '', // 
                manage_cost: '', // 
                property_right: '', // 
                come_from: '', // 
                brokerage: '', // 
                expiration: '', // 
                check_way: '', // 
                entrust_way: '', // 
                rent_charge1: '', // ~/
                rent_charge2: '', // ~/
                sale_charge1: '', // ~/
                sale_charge2: '', // 
                base_charge: '', // 
                status: '0', // 0123456
                uid1: 0,
                user_name1: '', //  
                user_mobile1: '', //  
                remark1: '', //  
                uid2: 0,
                user_name2: '', //  
                user_mobile2: '', //  
                remark2: '', //  
            },
            form_rules: {
                room_id: [
                    { validator: validate_room_id, trigger: 'change'}
                ],
                resource_name: [
                    { required: true, message: '', trigger: 'change'}
                ],
                room_feature: [
                    { required: true, message: '', trigger: 'change'}
                ],
                rent_charge: [
                    { validator: validate_rent_charge, trigger: 'blur'}
                ],
                sale_charge: [
                    { validator: validate_sale_charge, trigger: 'blur'}
                ]
            },
            room_decoration: [], // 
            room_feature: [], //  - 
            room_toward: [], //  - 
            room_unit: [], //  - 
            estateVal: [], // 
            estate: [], // 
            statusBase: [{
                name: '',
                value: 0
            },{
                name: '',
                value: 1
            },{
                name: '',
                value: 2
            },{
                name: '',
                value: 3
            },{
                name: '',
                value: 4
            },{
                name: '',
                value: 5
            },{
                name: '',
                value: 6
            }]
        }
    },
    props: {
        id: {
            type: Number
        }
    },
    created() {
        let _this = this;
        this.$https.all([
            this.getDic('pms_type_decoration', 'room_decoration'), // 
            this.getDic('pms_type_roomfunc', 'room_feature'), // 
            this.getDic('pms_type_direction', 'room_toward'), // 
            this.getDic('pms_type_unit', 'room_unit'), // 
            this.getProjects()
        ]).then(this.$https.spread(function (acct, perms) {
            if (_this.id) { // 
                _this.getOldData();
            }
        }))
    },
    watch: {
    },
    methods: {
        handleClick(tab) {
            this.house_type = tab.name;
        },
        getProjects() { // 
            this.$https.get('/api/pms_base_projects/GetProjects', {
                params: {
                    page: 1,
                    page_size: 999,
                }
            }).then((result) => {
                if (result.data.code == 0) {
                    for(var item of result.data.data.Items) {
                        let _item = {
                            value: item.project_id,
                            label: item.project_name,
                            children: []
                        }
                        this.estate.push(_item);
                    }
                } else {
                    this.$message({
                        type: 'error',
                        showClose: true,
                        message: result.data.message
                    })
                }
            })
        },
        getBuildings(project_id) { // 
            this.$https.get('/api/pms_base_buildings/GetBuildings', {
                params: {
                    page: 1,
                    page_size: 999,
                    project_id: project_id
                }
            }).then((result) => {
                if (result.data.code == 0) {
                    // 
                    for(let i = 0; i < this.estate.length; i++) {
                        //  id   children 
                        if (this.estate[i].value === project_id) {
                            this.estate[i].children = []; // 
                            for(let item of result.data.data.Items) {
                                let _item = {
                                    value: item.building_id,
                                    label: item.building_name,
                                    children: []
                                }
                                this.estate[i].children.push(_item);
                            }
                        }
                    }
                } else {
                    this.$message({
                        type: 'error',
                        showClose: true,
                        message: result.data.message
                    })
                }
            })
        },
        getFloors(project_id, building_id) { // 
            this.$https.get('/api/pms_base_floors/GetFloors', {
                params: {
                    page: 1,
                    page_size: 999,
                    building_id: building_id
                }
            }).then((result) => {
                if (result.data.code == 0) {

                    // 
                    for(let i = 0; i < this.estate.length; i++) {

                        //  id  
                        if (this.estate[i].value === project_id) {

                            for (let j = 0; j <this.estate[i].children.length; j++) {
                                //  ID 
                                if (this.estate[i].children[j].value === building_id) {
                                    //   
                                    this.estate[i].children[j].children = []; // 
                                    for(let item of result.data.data.Items) {
                                        let _item = {
                                            value: item.floor_id,
                                            label: item.floor_name,
                                            children: []
                                        }
                                        this.estate[i].children[j].children.push(_item);
                                    }
                                }
                            }
                            
                        }
                    }
                } else {
                    this.$message({
                        type: 'error',
                        showClose: true,
                        message: result.data.message
                    })
                }
            })
        },
        getRooms(project_id, building_id, floor_id) { // 

            this.$https.get('/api/pms_base_rooms/GetRooms', {
                params: {
                    page: 1,
                    page_size: 999,
                    floor_id: floor_id
                }
            }).then((result) => {
                if (result.data.code == 0) {
                    
                    // 
                    for(let i = 0; i < this.estate.length; i++) {

                        //  id  
                        if (this.estate[i].value === project_id) {

                            for (let j = 0; j <this.estate[i].children.length; j++) {
                                //  ID 
                                if (this.estate[i].children[j].value === building_id) {

                                    // 
                                    for (let k = 0; k <this.estate[i].children[j].children.length; k++) {

                                        //  ID 
                                        if (this.estate[i].children[j].children[k].value === floor_id) {
                                            //   
                                            this.estate[i].children[j].children[k].children = []; // 
                                            for(let item of result.data.data.Items) {
                                                let _item = {
                                                    value: item.room_id,
                                                    label: item.room_name
                                                }
                                                this.estate[i].children[j].children[k].children.push(_item);
                                            }
                                        }
                                    }
                                }
                            }
                            
                        }
                    }
                } else {
                    this.$message({
                        type: 'error',
                        showClose: true,
                        message: result.data.message
                    })
                }
            })
        },
        handleItemChange(val) {
            let activeItemLen = val.length;

            if (activeItemLen === 1) {
                this.getBuildings(val[0]);
            } else if (activeItemLen === 2) {
                this.getFloors(val[0], val[1]);
            } else if (activeItemLen === 3) {
                this.getRooms(val[0], val[1], val[2]);
            } else if (activeItemLen === 4) {

            }
        },
        handleChange() {
            let selectObj = this.getCascaderObj(this.estateVal, this.estate);
            this.form.resource_name = selectObj[0].label + '#' + selectObj[1].label + '#' + selectObj[2].label + '#' + selectObj[3].label; //  = 
            this.form.room_id = this.estateVal[3]; // ID
            //  room_id 
            this.$https.get('/api/pms_base_rooms/GetRoomDetail?room_id=' + this.form.room_id).then((result) => {
                if (result.data.code == 0) {
                    let _data = result.data.data;
                    this.form.room_feature = _data.room_feature == 0 ? '' :_data.room_feature.toString(); // 
                    this.form.room_decoration = _data.room_decoration == 0 ? '' :_data.room_decoration.toString(); // 
                    this.form.room_unit = _data.room_unit == 0 ? '' :_data.room_unit.toString(); // 
                    this.form.room_toward = _data.room_toward == 0 ? '' :_data.room_toward.toString(); // 
                    this.form.built_area = _data.built_area == 0 ? '' :_data.built_area.toString(); // 
                    this.form.usable_area = _data.usable_area == 0 ? '' :_data.usable_area.toString(); // 
                } else {
                    this.$message({
                        type: 'error',
                        showClose: true,
                        message: result.data.message
                    })
                }
            })
        },
        getOldData() { // 

            this.$https.get('/api/rent_base_resources/GetResource?resource_id=' + this.id).then((result) => {
                if (result.data.code == 0) {
                    let _data = result.data.data;
                    if (!_data.room_id) {
                        this.house_type = '1';
                        this.form.resource_name = _data.resource_name; // 
                        this.form.city = _data.city; // 
                        this.form.region = _data.region; // 
                        this.form.building = _data.building; // 
                        this.form.roomno = _data.roomno; // 
                        this.form.unit = _data.unit; // 
                        this.form.floor = _data.floor; // 
                        this.form.addr = _data.addr; // 
                        this.form.period = _data.period; // 
                    } else {
                        this.form.room_id = _data.room_id; // 
                        this.form.resource_name = _data.resource_name; // 
                    }
                    this.form.room_unit = _data.room_unit.toString(); // 
                    this.form.room_decoration = _data.room_decoration.toString(); // 
                    this.form.room_toward = _data.room_toward.toString(); // 
                    this.form.built_area = _data.built_area == 0 ? '' : _data.built_area.toString(); // 
                    this.form.usable_area = _data.usable_area == 0 ? '' : _data.usable_area.toString(); // 
                    this.form.room_feature = _data.room_feature.toString(); // 
                    console.log(this.form.room_feature)
                    this.form.building_age = _data.building_age; // 
                    this.form.manage_cost = _data.manage_cost; // 
                    this.form.property_right = _data.property_right; // 
                    this.form.come_from = _data.come_from; // 
                    this.form.brokerage = _data.brokerage; // 
                    this.form.expiration = _data.expiration; // 
                    this.form.check_way = _data.check_way; // 
                    this.form.entrust_way = _data.entrust_way; // 
                    if (_data.rent_charge || _data.rent_charge !== '~') {
                        this.form.rent_charge1 = _data.rent_charge.split('~')[0]; // ~/
                        this.form.rent_charge2 = _data.rent_charge.split('~')[0]; // ~/
                    }
                    if (_data.sale_charge || _data.sale_charge !== '~') {
                        this.form.sale_charge1 = _data.sale_charge.split('~')[0]; // ~/
                        this.form.sale_charge2 = _data.sale_charge.split('~')[0]; // ~/
                    }
                    this.form.base_charge = _data.base_charge; // 
                    this.form.status = _data.status.toString(); // 0123456
                    switch (_data.status) {
                        case 0:
                            this.statusBase = [{name: '',value: 0},{name: '',value: 1},{name: '',value: 2},{name: '',value: 5},{name: '',value: 6}]
                            break;
                        case 1:
                            this.statusBase = [{name: '',value: 0},{name: '',value: 1},{name: '',value: 2},{name: '',value: 5},{name: '',value: 6}]
                            break;
                        case 2:
                            this.statusBase = [{name: '',value: 0},{name: '',value: 1},{name: '',value: 2},{name: '',value: 5},{name: '',value: 6}]
                            break;
                        case 3:
                            this.statusBase = [{name: '',value: 3}]
                            break;
                        case 4:
                            this.statusBase = [{name: '',value: 4}]
                            break;
                        case 5:
                            this.statusBase = [{name: '',value: 0},{name: '',value: 1},{name: '',value: 2},{name: '',value: 5},{name: '',value: 6}]
                            break;
                        case 6:
                            this.statusBase = [{name: '',value: 0},{name: '',value: 1},{name: '',value: 2},{name: '',value: 5},{name: '',value: 6}]
                            break;
                        default:
                            this.statusBase = [{name: '',value: 0},{name: '',value: 1},{name: '',value: 2},{name: '',value: 3},{name: '',value: 4},{name: '',value: 5},{name: '',value: 6}]
                            break;
                    }
                    
                    if (_data.resource_users || _data.resource_users.length !== 0) {
                        for(let item of _data.resource_users) {
                            console.log(item.uid)
                            if (item.user_type === 1) {
                                this.form.uid1 = item.uid;
                                this.form.user_name1 = item.user_name;
                                this.form.user_mobile1 = item.user_mobile;
                                this.form.remark1 = item.remark;
                            } else {
                                this.form.uid2 = item.uid;
                                this.form.user_name2 = item.user_name;
                                this.form.user_mobile2 = item.user_mobile;
                                this.form.remark2 = item.remark;
                            }
                        }
                    }
                } else {
                    this.$message({
                        type: 'error',
                        showClose: true,
                        message: result.data.message
                    })
                }
            })
        },
        submit() { // 
            this.$refs.form.validate((valid) => {
                if (valid) {
                    let _postData = {
                        room_unit: this.form.room_unit, // 
                        room_decoration: this.form.room_decoration, // 
                        room_toward: this.form.room_toward, // 
                        room_feature: this.form.room_feature, // 
                        built_area: this.form.built_area == '' ? 0 : this.form.built_area, // 
                        usable_area: this.form.usable_area == '' ? 0 : this.form.usable_area, // 
                        building_age: this.form.building_age, // 
                        manage_cost: this.form.manage_cost, // 
                        property_right: this.form.property_right, // 
                        come_from: this.form.come_from, // 
                        brokerage: this.form.brokerage, // 
                        expiration: this.form.expiration, // 
                        check_way: this.form.check_way, // 
                        entrust_way: this.form.entrust_way, // 
                        rent_charge: this.form.rent_charge1 + '~' + this.form.rent_charge2, // ~/
                        sale_charge: this.form.sale_charge1 + '~' + this.form.sale_charge2, // 
                        base_charge: this.form.base_charge, // 
                        status: this.form.status // 0123456
                    }
                    if (this.house_type === '0') { // 
                        _postData.room_id = this.form.room_id;
                        _postData.resource_name = this.form.resource_name; // 
                    } else { // 
                        _postData.resource_name = this.form.resource_name; // 
                        _postData.city = this.form.city; // 
                        _postData.region = this.form.region; // 
                        _postData.building = this.form.building; // 
                        _postData.roomno = this.form.roomno; // 
                        _postData.unit = this.form.unit; // 
                        _postData.floor = this.form.floor; // 
                        _postData.addr = this.form.addr; // 
                        _postData.period = this.form.period; // 
                    }
                    _postData.resource_users = [{
                        resource_id: 0,
                        user_type: 1,// 12
                        user_name: this.form.user_name1, //  
                        user_mobile: this.form.user_mobile1, //  
                        remark: this.form.remark1 //  
                    },{
                        resource_id: 0,
                        user_type: 2,// 12
                        user_name: this.form.user_name2, //  
                        user_mobile: this.form.user_mobile2, //  
                        remark: this.form.remark2 //  
                    }];
                    console.log(this.form)
                    if (this.id) { // 
                        _postData.resource_id = parseInt(this.id);
                        _postData.resource_users[0].uid = this.form.uid1;
                        _postData.resource_users[1].uid = this.form.uid2;
                        _postData.resource_users[0].resource_id = parseInt(this.id);
                        _postData.resource_users[1].resource_id = parseInt(this.id);
                        this.$https.post('/api/rent_base_resources/UpdateResource', _postData).then((result) => {
                            if (result.data.code == 0) {
                                this.$message({
                                    type: 'success',
                                    showClose: true,
                                    message: '',
                                    duration: 1500,
                                    onClose: () => {
                                        // dialog
                                        this.closeDialog('dialogRentingEdit');
                                        // 
                                        this.$emit('parentGetDataList');
                                    }
                                })
                            } else {
                                this.$message({
                                    type: 'error',
                                    showClose: true,
                                    message: result.data.message
                                })
                            }
                        })
                    } else { // 

                        this.$https.post('/api/rent_base_resources/CreateResource', _postData).then((result) => {
                            if (result.data.code == 0) {
                                this.$message({
                                    type: 'success',
                                    showClose: true,
                                    message: '',
                                    duration: 1500,
                                    onClose: () => {
                                        // dialog
                                        this.closeDialog('dialogRentingAdd');
                                        // 
                                        this.$emit('parentGetDataList');
                                    }
                                })
                            } else {
                                this.$message({
                                    type: 'error',
                                    showClose: true,
                                    message: result.data.message
                                })
                            }
                        })
                    }
                    

                } else {
                    console.error('');
                    return false;
                }
            });
        },
        getDic(code, dic) { //  
            this.$https.get('/api/Dicts/GetValues', {
                params: {
                    type_code: code
                }
            }).then((result) => {
                if (result.data.code == 0) {
                    this[dic] = result.data.data;
                } else {
                    this.$message({
                        type: 'error',
                        showClose: true,
                        message: result.data.message
                    })
                }
            })
        },
        getTreeDeepArr(key, treeData) { // 

            let arr = []; // 
            let returnArr = []; // 
            let depth = 0; // 
            // 
            function childrenEach(childrenData, depthN) {

                for (var j = 0; j < childrenData.length; j++) {

                    depth = depthN; //   

                    arr[depthN] = (childrenData[j].id);
                    
                    if (childrenData[j].id == key) {

                        // returnArr = arr; // ,  
                        returnArr = arr.slice(0, depthN+1); //
                        break

                    } else {

                        if (childrenData[j].children) {

                            depth ++;
                            childrenEach(childrenData[j].children, depth);

                        }
                    }

                }
                return returnArr;
            }
            return childrenEach(treeData, depth);
        },
        closeDialog(name) { // 
            // this.$refs.form.resetFields(); // 
            if (!name) {
                this.$emit('closeDialog', 'dialogRentingAdd'); // 
                this.$emit('closeDialog', 'dialogRentingEdit'); // 
            } else {
                this.$emit('closeDialog', name); // 
            }
        }
    }
}
</script>
<style scoped>
.el-tag {
    margin-right: 10px;
}
</style>


