<template>
    <!--  ref="upload" -->
    <el-upload
        ref="upload"
        list-type="picture-card"
        multiple
        :limit="limit"
        :action="action"
        :data="access"
        :file-list="fileList"
        :accept="acceptTypes.join(',')"
        :before-upload="beforeUpload"
        :on-exceed="handleExceed"
        :on-remove="onRemove"
        :on-progress="onProgress"
        :on-success="onSuccess"
        :on-error="onError">
        <i class="el-icon-plus"></i>
    </el-upload>
</template>
<script>
import uploader from '@/mixins/uploader'

export default {
    mixins: [uploader],
    data: () => ({
        acceptTypes: ['image/png', 'image/jpeg', 'image/gif']
    }),
    methods: {
        // 
        beforeUpload (file) {
            let isPngJpg = this.acceptTypes.includes(file.type)
            if (!isPngJpg) {
                this.$notify.info({
                    title: file.name,
                    message: ' PNG / JPG / GIF '
                })
                return false
            }
            let isLt1M = file.size / 1024 / 1024 < 1
            if (!isLt1M) {
                this.$notify.info({
                    title: file.name,
                    message: ' 1 MB '
                })
                return false
            }
            return this.addFile(file)
        },
        handleExceed(files, fileList) {
            this.$message.warning(` ${this.limit}  ${files.length}  ${files.length + fileList.length} `);
        }
    }
}
</script>
<style scoped>
</style>


