import urljoin from 'url-join'
import debounce from 'lodash/debounce'
import browserMD5File from 'browser-md5-file'
import addSeconds from 'date-fns/add_seconds'
import axios from 'axios'
const isStr = s => typeof s === 'string'
// OSS 
const oss = {
	dir: 'sales', // 
	host: '', //  OSS 
	expiretime: '', // 13 
	policy: '', // Base64 
	signature: '', // 28 
	OSSAccessKeyId: '' // 16 
}

export default {
	props: {
		//  .sync  'url'  ['url1', 'url2', ...]
		files: { type: [String, Array], required: true },
		limit: { type: [Number], default: 9 } // 9
	},
	data: () => ({
		oss,
		key: '', //  keycomputed:access 
		percent: 0, // 
		taskQueue: [], //  Promise 
		isUploading: false,
		fileList: [] //  ElUpload  $props.fileList
	}),
	computed: {
		action () { //  ElUpload  $props.action
			return oss.host
		},
		access () { //  ElUpload  $props.data
			return {
				key: this.key,
				policy: oss.policy,
				signature: oss.signature,
				OSSAccessKeyId: oss.OSSAccessKeyId,
				success_action_status: 201
			}
		}
	},
	watch: {
		files: {
			handler (files) {
				if (isStr(files)) {
					files = [files]
				}
				//  ElUpload  $props.fileList  [{ name, url }] 
				this.fileList = files.map((url, idx) => ({ name: '' + idx, url }))
			},
			immediate: true
		},
		isUploading (isUploading) {
			// isUploading  true  false  nextTick  ElUpload $data.uploadFiles  $props.files
			//  nextTick onSuccess  this.nextFile()  file.url = uploadFile 
			isUploading || this.$nextTick(() => {
				this.syncUploadFiles()
			})
		}
	},
	methods: {
		/**
		 *  ElUpload  before-upload 
		 * @param  {File}
		 * @return {Boolean/Promise} -  false  Promise 
			beforeUpload (file) {
				//  file 
				return this.addFile(file)
			}
		 */
		syncUploadFiles () {
			//  blob  URL files 
			let files = this.$refs.upload.uploadFiles.map(({ url }) => url).filter(url => url.startsWith('http'))

			//  multipleElUpload  $data.uploadFiles 
			//  $props.files  files  undefined
			this.$emit('update:files', isStr(this.files) ? files.slice(-1)[0] || '' : files)

		},
		//  ElUpload  on-progress
		onProgress ({ percent }) {
			console.log('',percent);
			this.percent = ~~percent
		},
		//  ElUpload  on-success
		onSuccess (res, file, uploadFiles) {
			const uploadPath = this.nextFile()
			file.url = uploadPath //  blob  CDN 
		},
		//  ElUpload  on-remove
		onRemove: debounce(function () { // on-remove
			// 
			//  5  3  10 
			//  8  8  debounce 
			
			//  syncUploadFiles
			// Uncaught TypeError: Cannot set property 'status' of null
			// 
			//  2 ElUpload  handleProgress 
			//  syncUploadFiles  ElUpload $data.uploadFiles 
			//  handleProgress  var file = this.getFile(rawFile)  null
			//  file.status = 'uploading' 
			//  https://github.com/ElemeFE/element/blob/1.x/packages/upload/src/index.vue#L141-L146
			this.isUploading
				? setTimeout(() => this.onRemove, 1000)
				: this.syncUploadFiles()
		}, 250),
		//  ElUpload  on-error OSS access 
		onError () {
			this.isUploading = false //  auto run 
			this.$message.warning('')
		},
		addFile (file) {
			return new Promise(resolve => {
				this.taskQueue.push({ file, start: resolve })

				// auto run
				if (!this.isUploading) {
					this.isUploading = true
					this.nextFile(true)
				}
			})
		},
		nextFile (isAutorun) {
			//  isUploading false => true auto run
			// 1.  this.action  this.key  '' join  '/'
			// 2.  uploadPath
			//  auto run  urljoin
			let uploadPath
			if (!isAutorun) {
				uploadPath = urljoin(this.action, this.key)
			}
			// 
			//  if(__DEV__ && !isAutorun) UglifyJS  
			// if (__DEV__) {
			// 	if (!isAutorun) {
			// 		console.info('', uploadPath)
			// 	}
			// }

			if (!isAutorun) {
				console.info('', uploadPath)
			}
			const { taskQueue } = this
			if (taskQueue.length) {
				const ensureAccessValid = isAccessExpired() ? updateAccess : doNothing
				let nextTask
				ensureAccessValid().then(() => {
					nextTask = taskQueue.shift()
					return keygen(nextTask.file)
				}).then(key => {
					this.key = key //  key  computed:access
					this.$nextTick(() => {
						nextTask.start() //  resolve  before-upload  promise
					})
				}).catch(e => console.warn(e))
			} else {
				this.isUploading = false
			}
			
			return uploadPath
		}
	}
}

//  access  10 
function isAccessExpired () {
	return +addSeconds(new Date(), 10) > + oss.expiretime
}

/**
 *  OSS access
 * @return {Promise}
 */
function updateAccess() {
	return axios.get('http://dev.crm.zhiling.net.cn/api/Ali/Sign').then(re => {
		Object.assign(oss, re.data.data);
	})
}

function doNothing () {
	return Promise.resolve()
}

/**
 *  key
 * @param   {File}
 * @resolve {String}  '<>/3d3e93a9745fd21240ef3c88045cc0d1.jpg'
 */
function keygen(file) {
	return new Promise((resolve, reject) => {
		browserMD5File(file, (err, md5) => {
			if (err) {
				reject(err)
				return
			}
			resolve(
				// urljoin(oss.dir, `${md5}.${file.name.split('.').pop()}`)
				urljoin(oss.dir, `${md5}.${file.name.split('.').pop()}`)
			)
		})
	})
}


