<template>
    <div>
        <el-form ref="form" :model="form" label-width="140px" label-suffix="" size="small">

            <el-row v-if="form.customer_classify !== '2'">
                <el-col :span="24">
                    <el-form-item label="/">
                        <p>{{form.theme}}</p>
                    </el-form-item>
                </el-col>
            </el-row>
            <el-row>
                <el-col :span="12">
                    <el-form-item label="" prop="classification">
                        <p>{{getDicVals(form.classification.join(','), 'classification')}}</p>
                    </el-form-item>
                </el-col>
                <el-col :span="12">
                    <el-form-item label="" prop="customer_source">
                        <p>{{getDicVals(form.customer_source, 'customer_source')}}</p>
                    </el-form-item>
                </el-col>
            </el-row>
            <el-row v-if="form.customer_classify !== '2'">
                <el-col :span="12">
                    <el-form-item label="" prop="purchase_intention">
                        <p>{{getDicVals(form.purchase_intention, 'purchase_intention')}}</p>
                    </el-form-item>
                </el-col>
                <el-col :span="12">
                    <el-form-item label="">
                        <p>{{form.intentional_area}}</p>
                    </el-form-item>
                </el-col>
            </el-row>
            <el-row v-if="form.transactions_num > 0">
                <el-col :span="12">
                    <el-form-item label="" prop="transactions_num">
                        <p>{{form.transactions_num}}</p>
                    </el-form-item>
                </el-col>
                <el-col :span="12">
                    <el-form-item label="" prop="enterprise_name">
                        <p>{{form.enterprise_name}}</p>
                    </el-form-item>
                </el-col>
            </el-row>
            <el-row>
                <el-col :span="12">
                    <el-form-item label="" prop="customer_name">
                        <p>{{form.customer_name}}</p>
                    </el-form-item>
                </el-col>
                <el-col :span="12">
                    <el-form-item label="" prop="telephone">
                        <p>{{form.telephone}}</p>
                    </el-form-item>
                </el-col>
            </el-row>
            <el-row v-if="form.customer_type !== '1'">
                <el-col :span="12">
                    <el-form-item label="" prop="customer_name">
                        <p>{{form.customer_type === '1' ? '' : ''}}</p>
                    </el-form-item>
                </el-col>
            </el-row>
            <el-row>
                <el-col :span="12">
                    <el-form-item label="">
                        <p>{{(form.sex === '1' ? '' : '')}}</p>
                    </el-form-item>
                </el-col>
                <el-col :span="12">
                    <el-form-item label="">
                        <p>{{form.native_place}}</p>
                    </el-form-item>
                </el-col>
            </el-row>
            <el-row>
                <el-col :span="12">
                    <el-form-item label="">
                        <p>{{form.company_email}}</p>
                    </el-form-item>
                </el-col>
                <el-col :span="12">
                    <el-form-item label="">
                        <p>{{form.job}}</p>
                    </el-form-item>
                </el-col>
            </el-row>
            <el-row>
                <el-col :span="12">
                    <el-form-item label="">
                        <p>{{getDicVals(form.person_identification_type, 'identification_type')}}</p>
                    </el-form-item>
                </el-col>
                <el-col :span="12">
                    <el-form-item label="" prop="person_identification_number">
                        <p>{{form.person_identification_number}}</p>
                    </el-form-item>
                </el-col>
            </el-row>

            <el-row>
                <el-col :span="24">
                    <el-form-item label="">
                        <p>
                            <img width="120px" height="120px" v-for="(item, index) in form.person_identification_image" :key="index" :src="item" v-if="item">
                        </p>
                    </el-form-item>
                </el-col>
            </el-row>
            <el-row>
                <el-col :span="24">
                    <el-form-item label="">
                        <p>{{form.communication_address}}</p>
                    </el-form-item>
                </el-col>
            </el-row>
            <el-row>
                <el-col :span="24">
                    <el-form-item label="Logo">
                        <p>
                            <img width="120px" height="120px" :src="form.company_logo" v-if="form.company_logo">
                        </p>
                    </el-form-item>
                </el-col>
            </el-row>
            <el-row>
                <el-col :span="12">
                    <el-form-item label="">
                        <p>{{getDicVals(form.company_identification_type, 'company_card_type')}}</p>
                    </el-form-item>
                </el-col>
                <el-col :span="12">
                    <el-form-item label="">
                        <p>{{form.company_identification_number}}</p>
                    </el-form-item>
                </el-col>
            </el-row>

            <el-row>
                <el-col :span="24">
                    <el-form-item label="">
                        <p>
                            <img width="120px" height="120px" v-for="(item, index) in form.company_identification_image" :key="index" :src="item" v-if="item">
                        </p>
                    </el-form-item>
                </el-col>
            </el-row>
            <el-row>
                <el-col :span="12">
                    <el-form-item label="">
                        <p>{{form.company_corporate}}</p>
                    </el-form-item>
                </el-col>
                <el-col :span="12">
                    <el-form-item label="">
                        <p>{{getDicVals(form.company_industry_type, 'company_industry')}}</p>
                    </el-form-item>
                </el-col>
            </el-row>
            <el-row>
                <el-col :span="12">
                    <el-form-item label="">
                        <p>{{getDicVals(form.company_type, 'company_type')}}</p>
                    </el-form-item>
                </el-col>
                <el-col :span="12">
                    <el-form-item label="">
                        <p>{{form.company_registered_capital}}</p>
                    </el-form-item>
                </el-col>
            </el-row>
            <el-row>
                <el-col :span="12">
                    <el-form-item label="">
                        <p>{{form.company_main_product}}</p>
                    </el-form-item>
                </el-col>
                <el-col :span="12">
                    <el-form-item label="()">
                        <p>{{getDicVals(form.company_employees_number, 'company_employees')}}</p>
                    </el-form-item>
                </el-col>
            </el-row>
            <el-row>
                <el-col :span="24">
                    <el-form-item label="">
                        <p>{{form.company_registered_address}}</p>
                    </el-form-item>
                </el-col>
            </el-row>
            <el-row>
                <el-col :span="24">
                    <el-form-item label="">
                        <p>{{form.company_scope_of_operation}}</p>
                    </el-form-item>
                </el-col>
            </el-row>

        </el-form>

        <h3 class="table_title"></h3>
        <el-table :data="tableData" style="width: 100%" border size="mini">
            <el-table-column prop="linkman_name" label="" width="90"></el-table-column>
            <el-table-column prop="telephone" label=""></el-table-column>
            <el-table-column prop="identification_number" label=""></el-table-column>
            <el-table-column label="">
                <template slot-scope="scope">
                    {{getDicVals(scope.row.identification_type,'identification_type')}}
                </template>
            </el-table-column>
            <el-table-column prop="remark" label=""></el-table-column>
            <el-table-column prop="created_time" label="" width="140"></el-table-column>
        </el-table>
        <div class="text-center m-t">
            <el-button @click="closeDialog()"> </el-button>
        </div>
    </div>
</template>

<script>
export default {
    data () {
        return {
            form: {
                theme: '', // 
                customer_classify: '0', //  0  1 
                customer_type: '0', // 1 2 
                transactions_num: '', // 
                classification: [], // 
                customer_source: '', // 
                purchase_intention: '', // 
                intentional_area: '', // 
                enterprise_name: '', // 
                customer_name: '', // 
                telephone: '', // 
                sex: '1', //  1 0
                native_place: '', //
                company_email: '', // 
                job: '', // 
                person_identification_type: '', // 
                person_identification_number: '', // 
                person_identification_image: [], // 
                communication_address: '', // 
                company_logo: '', // logo
                company_identification_type: '', // 
                company_identification_number: '', // 
                company_identification_image: [], // 
                company_corporate: '', // 
                company_industry_type: '', // 
                company_registered_capital: '', // 
                company_registered_address: '', // 
                company_type: '', // 
                company_main_product: '', // 
                company_employees_number: '', // 
                company_scope_of_operation: '', // 
            },
            tableData: [], // 
            classification: [], //  - 
            customer_source: [], //  - 
            identification_type: [], //  - 
            company_card_type: [], //  - 
            company_industry: [], //  - 
            company_type: [], //  - 
            company_employees: [], //  - 
            purchase_intention: [] //  - 
        }
    },
    props: {
        id: {
            type: Number
        }
    },
    created() {
        let _this = this;
        this.$https.all([
            this.getDic('crm_classification', 'classification'),
            this.getDic('crm_customer_source', 'customer_source'),
            this.getDic('contact_card_type', 'identification_type'),
            this.getDic('company_card_type', 'company_card_type'),
            this.getDic('company_industry', 'company_industry'),
            this.getDic('company_type', 'company_type'),
            this.getDic('company_employees', 'company_employees'),
            this.getDic('crm_purchase_intention', 'purchase_intention')
        ]).then(this.$https.spread(function (acct, perms) {
            if (_this.id) { // 
                _this.getOldData();
                _this.getContactsList();
            }
        }))
    },
    methods: {
        getOldData() { // 

            this.$https.get('/api/Customer/get_customerdetails?c_id=' + this.id).then((result) => {
                if (result.data.code == 0) {
                    let _data = result.data.data;
                    
                    this.form.theme = _data.theme;// 
                    this.form.customer_classify = _data.customer_classify;// 
                    this.form.customer_type = _data.customer_type;// z
                    
                    this.form.classification = _data.classification.split(',');// 
                    this.form.customer_source = _data.customer_source; // 
                    this.form.purchase_intention = _data.purchase_intention; // 
                    this.form.intentional_area = _data.intentional_area == 0 ? '' : _data.intentional_area; // 
                    this.form.transactions_num = _data.transactions_num; // 
                    this.form.enterprise_name = _data.enterprise_name; // 
                    this.form.customer_name = _data.customer_name; // 
                    this.form.telephone = _data.telephone; // 
                    this.form.sex = _data.sex.toString(); //  1 0
                    this.form.native_place = _data.native_place; // 
                    this.form.company_email = _data.company_email; // 
                    this.form.job = _data.job; // 
                    this.form.person_identification_type = _data.person_identification_type; // 
                    this.form.person_identification_number = _data.person_identification_number; // 
                    if(_data.person_identification_image) {
                        this.form.person_identification_image = _data.person_identification_image.split(',');// 
                    }
                    
                    
                    this.form.company_logo = _data.company_logo; // logo
                    this.form.company_identification_type = _data.company_identification_type; // 

                    this.form.company_industry_type = _data.company_industry_type == 0 ? '' : _data.company_industry_type; // 
                    this.form.company_type = _data.company_type == 0 ? '' : _data.company_type; // 
                    this.form.company_employees_number = _data.company_employees_number == 0 ? '' : _data.company_employees_number; // 
                    
                    this.form.company_identification_number = _data.company_identification_number; // 
                    if(_data.company_identification_image) {
                        this.form.company_identification_image = _data.company_identification_image.split(',');// 
                    }
                    this.form.communication_address = _data.communication_address; // 

                    this.form.company_corporate = _data.company_corporate; // 
                    this.form.company_registered_capital = _data.company_registered_capital; // 
                    this.form.company_registered_address = _data.company_registered_address; // 
                    this.form.company_main_product = _data.company_main_product; // 
                    this.form.company_scope_of_operation = _data.company_scope_of_operation; // 
                    
                    console.log(this.form);
                } else {
                    this.$message({
                        type: 'error',
                        showClose: true,
                        message: result.data.message
                    })
                }
            })
        },
        getContactsList() { // 

            this.$https.get('/api/customer_linkman/list_customer_linkman?c_id=' + this.id).then((result) => {
                if (result.data.code == 0) {
                    this.tableData = result.data.data;
                } else {
                    this.$message({
                        type: 'error',
                        showClose: true,
                        message: result.data.message
                    })
                }
            })
        },
        getDic(code, dic) { //  
            this.$https.get('/api/Dicts/GetValues', {
                params: {
                    type_code: code
                }
            }).then((result) => {
                if (result.data.code == 0) {
                    this[dic] = result.data.data;
                } else {
                    this.$message({
                        type: 'error',
                        showClose: true,
                        message: result.data.message
                    })
                }
            })
        },
        getDicVals(keys, dic) { // 
            let texts = [];
            let keysArr = keys.toString().split(',');
            for(var keyItem of keysArr) {
                for(var item of this[dic]) {
                    if (keyItem == item.value_id) {
                        texts.push(item.value_name);
                    }
                }
            }
            return texts.join(', ');
        },
        closeDialog(name) { // 
            // this.$refs.form.resetFields(); // 
            if (!name) {
                this.$emit('closeDialog', 'dialogDetails'); // 
            } else {
                this.$emit('closeDialog', name); // 
            }
        }
    }
}
</script>
<style scoped>
    .el-tag {
        margin-right: 10px;
    }

    .avatar-uploader {
        display: block;
    }
    .avatar-uploader /deep/ .el-upload {
        width: 120px;
        height: 120px;
        line-height: 120px;
    }
    .avatar-uploader /deep/ .el-upload-list__item {
        width: 120px;
        height: 120px;
    }
    .avatar-uploader /deep/ .el-progress,.avatar-uploader /deep/ .el-progress-circle {
        width: 120px !important;
        height: 120px !important;
    }
    /* el-progress--circle */
    .table_title {
        display: inline-block;
        margin-top: 30px;
        margin-bottom: 30px;
        color: #666;
        font-weight: normal;
        padding-left: 5px;
        border-left: 2px solid #4E97D9;
        line-height: 1.2;
    }
</style>


