<template>
    <div>
        <el-form ref="form" :model="form" :rules="rules" label-width="120px">
            <el-form-item label="">
                <p>{{form.resource_name}}</p>
            </el-form-item>
            <el-form-item label="" prop="customer_id">
                <el-select v-model="form.customer_id" @change="changeCustomer" filterable placeholder="">
                    <el-option
                        v-for="item in myCustomers"
                        :key="item.c_id"
                        :label="item.customer_name"
                        :value="item.c_id">
                    </el-option>
                </el-select>
            </el-form-item>
            <el-form-item label="" prop="view_result">
                <el-input v-model="form.view_result" type="textarea" :rows="4"></el-input>
            </el-form-item>
            <el-form-item label="" prop="view_date">
                <el-date-picker v-model="form.view_date" :editable="false" type="date" placeholder="" format="yyyy-MM-dd" value-format="yyyy-MM-dd"></el-date-picker>
            </el-form-item>
            <el-form-item>
                <el-button type="primary" @click="submit()"></el-button>
                <el-button @click="closeDialog()"></el-button>
            </el-form-item>
        </el-form>
    </div>
</template>

<script>
export default {
    props: {
        resourceData: {
            type: Object
        }
    },
    data () {
        return {
            form: {
                customer_name: '', // 
                view_date: '', // 
                customer_id: '', // 
                customer_mobile: '', // 
                view_result: '', // 
                resource_id: '', // 
                resource_name: '', // 
            },
            rules: {
                customer_id: [
                    { required: true, message: '', trigger: 'blur' }
                ],
                view_date: [
                    { required: true, message: '', trigger: 'change' }
                ],
                view_result: [
                    { required: true, message: '', trigger: 'blur' }
                ]
            },
            myCustomers: [] // 
        }
    },
    created() {
        this.form.resource_id = this.resourceData.id;
        this.form.resource_name = this.resourceData.name;

        let _this = this;
        this.$https.all([
            this.getMyCustomer()
        ]).then(this.$https.spread(function (acct, perms) {
        }))
    },
    methods: {
        getMyCustomer() { //  list
            this.$https.get('/api/Customer/page', {
                params: {
                    currentPage: 1,
                    pageSize: 9999,
                    customer_classify: 999,
                    user_id: -2
                }
            }).then((result) => {
                if (result.data.code == 0) {
                    this.myCustomers = result.data.data.Items;
                } else {
                    this.$message({
                        type: 'error',
                        showClose: true,
                        message: result.data.message
                    })
                }
            })
        },
        changeCustomer(val) {
            let obj = {};
            obj = this.myCustomers.find((item) => {
                return item.c_id === val;
            })
            console.log(obj)
            console.log(this.form)
            this.form.customer_name = obj.customer_name;
            this.form.customer_mobile = obj.telephone;
        },
        submit() { // 
            this.$refs.form.validate((valid) => {
                if (valid) {
                    let _postData = JSON.parse(JSON.stringify(this.form));
                    console.log(this.form)
                    this.$https.post('/api/rent_base_resources/CreateView', _postData).then((result) => {
                        if (result.data.code == 0) {
                            this.$message({
                                type: 'success',
                                showClose: true,
                                message: '',
                                duration: 1500,
                                onClose: () => {
                                    // dialog
                                    this.closeDialog();
                                    // 
                                    this.$emit('parentGetDataList');
                                }
                            })
                        } else {
                            this.$message({
                                type: 'error',
                                showClose: true,
                                message: result.data.message
                            })
                        }
                    })
                    

                } else {
                    console.error('');
                    return false;
                }
            });
        },
        closeDialog(name) {
            if (!name) {
                this.$emit('closeDialog', 'dialogGetViewsAdd'); // 
            } else {
                this.$emit('closeDialog', name); // 
            }
        }
    }
}

</script>

<style scoped>
</style>



