<template>
    <div>
        <el-tabs :value="house_type" type="card">
            <el-tab-pane label="" name="0" v-if="house_type === '0'"></el-tab-pane>
            <el-tab-pane label="" name="1" v-else></el-tab-pane>
        </el-tabs>
        <el-form ref="form" ::model="form" label-width="100px" size="small" label-suffix="">
            <el-form-item label="" v-show="house_type === '0' && id"> 
                <p>{{form.resource_name}}</p>
            </el-form-item>
            <div v-show="house_type === '1'">
                <el-row>
                    <el-col :span="8">
                        <el-form-item label="">
                            <p>{{form.city}}</p>
                        </el-form-item>
                    </el-col>
                    <el-col :span="8">
                        <el-form-item label="">
                            <p>{{form.region}}</p>
                        </el-form-item>
                    </el-col>
                    <el-col :span="8">
                        <el-form-item label="">
                            <p>{{form.resource_name}}</p>
                        </el-form-item>
                    </el-col>
                </el-row>
                <el-row>
                    <el-col :span="6">
                        <el-form-item label="">
                            <p>{{form.building}}</p>
                        </el-form-item>
                    </el-col>
                    <el-col :span="6">
                        <el-form-item label="">
                            <p>{{form.unit}}</p>
                        </el-form-item>
                    </el-col>
                    <el-col :span="6">
                        <el-form-item label="">
                            <p>{{form.floor}}</p>
                        </el-form-item>
                    </el-col>
                    <el-col :span="6">
                        <el-form-item label="">
                            <p>{{form.roomno}}</p>
                        </el-form-item>
                    </el-col>
                </el-row>
                <el-row>
                    <el-col :span="18">
                        <el-form-item label="">
                            <p>{{form.addr}}</p>
                        </el-form-item>
                    </el-col>
                    <el-col :span="6">
                        <el-form-item label="">
                            <p>{{form.period}}</p>
                        </el-form-item>
                    </el-col>
                </el-row>
            </div>
            
            <div class="hr-line-dashed"></div>        
            <el-row>
                <el-col :span="12">
                    <el-form-item label="">
                        <p>{{getDicVals(form.room_unit, 'room_unit')}}</p>
                    </el-form-item>
                </el-col>
                <el-col :span="12">
                    <el-form-item label="">
                        <p>{{getDicVals(form.room_decoration, 'room_decoration')}}</p>
                    </el-form-item>
                </el-col>
            </el-row>
            <el-row>
                <el-col :span="12">
                    <el-form-item label="">
                        <p>{{getDicVals(form.room_toward, 'room_toward')}}</p> 
                    </el-form-item>
                </el-col>
                <el-col :span="12">
                    <el-form-item label="" prop="room_feature">
                        <p>{{getDicVals(form.room_feature, 'room_feature')}}</p>
                    </el-form-item>
                </el-col>
            </el-row>
            <el-row>
                <el-col :span="12">
                    <el-form-item label=""> 
                        <p>{{form.built_area}} </p>
                    </el-form-item>
                </el-col>
                <el-col :span="12">
                    <el-form-item label="">
                        <p>{{form.usable_area}} </p>
                    </el-form-item>
                </el-col>
            </el-row>
            <el-row>
                <el-col :span="12">
                    <el-form-item label="">
                        <p>{{form.building_age}}</p>
                    </el-form-item>
                </el-col>
                <el-col :span="12">
                    <el-form-item label="">
                        <p>{{form.manage_cost}} /</p>
                    </el-form-item>
                </el-col>
            </el-row>
            <el-row>
                <el-col :span="12">
                    <el-form-item label="">
                        <p>{{form.property_right}}</p>
                    </el-form-item>
                </el-col>
                <el-col :span="12">
                    <el-form-item label="">
                        <p>{{form.come_from}}</p>
                    </el-form-item>
                </el-col>
            </el-row>
            <el-row>
                <el-col :span="12">
                    <el-form-item label="">
                        <p>{{form.brokerage}}</p>
                    </el-form-item>
                        
                </el-col>
                <el-col :span="12">
                    <el-form-item label="">
                        <p>{{form.expiration}}</p>
                    </el-form-item>
                </el-col>
            </el-row>
            <el-row>
                <el-col :span="12">
                    <el-form-item label="">
                        <p>{{form.check_way}}</p>
                    </el-form-item>
                </el-col>
                <el-col :span="12">
                    <el-form-item label="">
                        <p>{{form.entrust_way}}</p>
                    </el-form-item>
                </el-col>
            </el-row>
            <el-row>
                <el-col :span="12">
                    <el-form-item label="">
                        <p v-if="form.status === 0"></p>
                        <p v-else-if="form.status === 1"></p>
                        <p v-else-if="form.status === 2"></p>
                        <p v-else-if="form.status === 3"></p>
                        <p v-else-if="form.status === 4"></p>
                        <p v-else-if="form.status === 5"></p>
                        <p v-else-if="form.status === 6"></p>
                        <p v-else></p>
                    </el-form-item>
                </el-col>
                <el-col :span="12">
                    <el-form-item label="">
                        <p>{{form.rent_charge}} /</p>
                    </el-form-item>
                </el-col>
            </el-row>
            <el-row>
                <el-col :span="12">
                    <el-form-item label="">
                        <p>{{form.sale_charge}} /</p>
                    </el-form-item>
                </el-col>
                <el-col :span="12">
                    <el-form-item label="">
                        <p>{{form.base_charge}} /</p>
                    </el-form-item>
                </el-col>
            </el-row>
                    
            <div class="hr-line-dashed"></div>
            <el-row>
                <el-col :span="24">
                    <table class="renting-details-table">
                        <tr>
                            <td width="100"></td>
                            <td></td>
                            <td></td>
                            <td></td>
                        </tr>
                        <tr>
                            <td></td>
                            <td>{{form.user_name1}}</td>
                            <td>{{form.user_mobile1}}</td>
                            <td>{{form.remark1}}</td>
                        </tr>
                        <tr>
                            <td></td>
                            <td>{{form.user_name2}}</td>
                            <td>{{form.user_mobile2}}</td>
                            <td>{{form.remark2}}</td>
                        </tr>
                    </table>
                </el-col>
            </el-row>
        </el-form>
    </div>
</template>

<script>
export default {
    data () {
        return {
            house_type: '0',
            form: {
                resource_name: '', // 
                city: '', // 
                region: '', // 
                building: '', // 
                roomno: '', // 
                unit: '', // 
                floor: '', // 
                addr: '', // 
                period: '', // 

                room_id: '', // 

                room_unit: '', // 
                room_decoration: '', // 
                room_toward: '', // 
                built_area: '', // 
                usable_area: '', // 
                room_feature: '', // 
                building_age: '', // 
                manage_cost: '', // 
                property_right: '', // 
                come_from: '', // 
                brokerage: '', // 
                expiration: '', // 
                check_way: '', // 
                entrust_way: '', // 
                rent_charge1: '', // ~/
                rent_charge2: '', // ~/
                sale_charge1: '', // ~/
                sale_charge2: '', // 
                base_charge: '', // 
                status: '0', // 0123456
                uid1: 0,
                user_name1: '', //  
                user_mobile1: '', //  
                remark1: '', //  
                uid2: 0,
                user_name2: '', //  
                user_mobile2: '', //  
                remark2: '', //  
            },
            room_decoration: [], // 
            room_feature: [], //  - 
            room_toward: [], //  - 
            room_unit: [], //  - 
            estateVal: [], // 
            estate: [] // 
        }
    },
    props: {
        id: {
            type: Number
        }
    },
    created() {
        let _this = this;
        this.$https.all([
            this.getDic('pms_type_decoration', 'room_decoration'), // 
            this.getDic('pms_type_roomfunc', 'room_feature'), // 
            this.getDic('pms_type_direction', 'room_toward'), // 
            this.getDic('pms_type_unit', 'room_unit') // 
        ]).then(this.$https.spread(function (acct, perms) {
            if (_this.id) { // 
                _this.getOldData();
            }
        }))
    },
    watch: {
    },
    methods: {
        getOldData() { // 

            this.$https.get('/api/rent_base_resources/GetResource?resource_id=' + this.id).then((result) => {
                if (result.data.code == 0) {
                    let _data = result.data.data;
                    if (!_data.room_id) {
                        this.house_type = '1';
                        this.form.resource_name = _data.resource_name; // 
                        this.form.city = _data.city; // 
                        this.form.region = _data.region; // 
                        this.form.building = _data.building; // 
                        this.form.roomno = _data.roomno; // 
                        this.form.unit = _data.unit; // 
                        this.form.floor = _data.floor; // 
                        this.form.addr = _data.addr; // 
                        this.form.period = _data.period; // 
                    } else {
                        this.form.room_id = _data.room_id; // 
                        this.form.resource_name = _data.resource_name; // 
                    }
                    this.form.room_unit = _data.room_unit.toString(); // 
                    this.form.room_decoration = _data.room_decoration.toString(); // 
                    this.form.room_toward = _data.room_toward.toString(); // 
                    this.form.built_area = _data.built_area == 0 ? '' : _data.built_area.toString(); // 
                    this.form.usable_area = _data.usable_area == 0 ? '' : _data.usable_area.toString(); // 
                    this.form.room_feature = _data.room_feature.toString(); // 
                    console.log(this.form.room_feature)
                    this.form.building_age = _data.building_age; // 
                    this.form.manage_cost = _data.manage_cost; // 
                    this.form.property_right = _data.property_right; // 
                    this.form.come_from = _data.come_from; // 
                    this.form.brokerage = _data.brokerage; // 
                    this.form.expiration = _data.expiration; // 
                    this.form.check_way = _data.check_way; // 
                    this.form.entrust_way = _data.entrust_way; // 

                    if (_data.rent_charge || _data.rent_charge !== '~') {
                        this.form.rent_charge = _data.rent_charge; // ~/
                    }
                    if (_data.sale_charge || _data.sale_charge !== '~') {
                        this.form.sale_charge = _data.sale_charge; // ~/
                    }
                    this.form.base_charge = _data.base_charge; // 
                    this.form.status = _data.status; // 0123456

                    if (_data.resource_users || _data.resource_users.length !== 0) {
                        for(let item of _data.resource_users) {
                            if (item.user_type === 1) {
                                this.form.uid1 = item.uid;
                                this.form.user_name1 = item.user_name;
                                this.form.user_mobile1 = item.user_mobile;
                                this.form.remark1 = item.remark;
                            } else {
                                this.form.uid2 = item.uid;
                                this.form.user_name2 = item.user_name;
                                this.form.user_mobile2 = item.user_mobile;
                                this.form.remark2 = item.remark;
                            }
                        }
                    }
                } else {
                    this.$message({
                        type: 'error',
                        showClose: true,
                        message: result.data.message
                    })
                }
            })
        },
        getDic(code, dic) { //  
            this.$https.get('/api/Dicts/GetValues', {
                params: {
                    type_code: code
                }
            }).then((result) => {
                if (result.data.code == 0) {
                    this[dic] = result.data.data;
                } else {
                    this.$message({
                        type: 'error',
                        showClose: true,
                        message: result.data.message
                    })
                }
            })
        },
        closeDialog(name) { // 
            // this.$refs.form.resetFields(); // 
            if (!name) {
                this.$emit('closeDialog', 'dialogRentingAdd'); // 
                this.$emit('closeDialog', 'dialogRentingEdit'); // 
            } else {
                this.$emit('closeDialog', name); // 
            }
        },
        getDicVals(keys, dic) { // 
            let texts = [];
            let keysArr = keys.toString().split(',');
            for(var keyItem of keysArr) {
                for(var item of this[dic]) {
                    if (keyItem == item.value_id) {
                        texts.push(item.value_name);
                    }
                }
            }
            return texts.join(', ');
        }
    }
}
</script>
<style scoped>
    .renting-details-table {
        width: 100%;
        border-spacing: 0;
        border-collapse: collapse;
    }
    .renting-details-table td{
        border: 1px solid #eee;
        padding: 8px;
    }
</style>


